* Refonte SCSS pour les formulaires Contact Form 7
* - bordures 1px solid $velvet
* - border-radius 2px
* - pas d'outline au focus
* - placeholder 0.9rem
* - utilitaire .form-half (50/50)
* - garde compatibilité avec Bootstrap via sélecteurs spécifiques
*/
.wpcf7 {
  max-width: 50rem;
  margin: 0 auto !important;
  /* --- Input / textarea styling --- */
  /* Focus : haute spécificité pour écraser Bootstrap */
  /* submit/button */
  /* --- 50/50 fields --- */
  /* --- Placeholder size --- */
  /* vendor fallbacks */
}
.wpcf7 .wpcf7-form p {
  margin-bottom: 1rem;
}
.wpcf7 .wpcf7-form label {
  width: 100%;
}
.wpcf7 .wpcf7-not-valid-tip {
  color: theme-color("danger");
}
.wpcf7 .wpcf7-validation-errors {
  color: theme-color("danger");
  border: 1px solid #e9ecef;
}
.wpcf7 input[type=text],
.wpcf7 input[type=email],
.wpcf7 input[type=tel],
.wpcf7 input[type=number],
.wpcf7 input[type=search],
.wpcf7 input[type=url],
.wpcf7 select,
.wpcf7 textarea,
.wpcf7 .wpcf7-form-control {
  font-size: 0.9rem !important;
  color: #272727 !important;
  border: none !important;
  border-radius: 2px !important;
  background-color: rgba(251, 210, 218, 0.3) !important;
  background-clip: padding-box;
  box-shadow: none !important;
  outline: none !important;
  transition: background-color 0.15s ease, border-color 0.15s ease, box-shadow 0.15s ease;
}
.wpcf7 .wpcf7-acceptance {
  background-color: transparent !important;
}
.wpcf7 textarea {
  resize: none;
}
.wpcf7 input.form-control:focus,
.wpcf7 textarea.form-control:focus,
.wpcf7 select.form-control:focus,
.wpcf7 input[type=text]:focus,
.wpcf7 input[type=email]:focus,
.wpcf7 input[type=tel]:focus,
.wpcf7 input[type=number]:focus,
.wpcf7 input[type=search]:focus,
.wpcf7 input[type=url]:focus,
.wpcf7 select:focus,
.wpcf7 textarea:focus,
.wpcf7 .wpcf7-form-control:focus {
  background-color: rgba(251, 210, 218, 0.5) !important;
  border-color: #bc3365 !important;
  outline: none !important;
  box-shadow: none !important;
}
.wpcf7 input[type=submit],
.wpcf7 button,
.wpcf7 .wpcf7-submit {
  border-radius: 2px;
}
.wpcf7 input[type=submit]:focus,
.wpcf7 button:focus,
.wpcf7 .wpcf7-submit:focus {
  outline: none !important;
  box-shadow: none !important;
}
.wpcf7 .form-half {
  display: inline-block;
  width: calc(50% - 8px);
