.logo-container .logo_svg {
  max-height: 4rem;
  width: auto;
  position: relative;
  vertical-align: middle;
}

#wrapper-navbar {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  background: #fff;
  transition: background 0.3s ease;
  box-shadow: 0 2px 16px rgba(0, 0, 0, 0.04);
}
#wrapper-navbar .bi {
  font-size: 1.5rem;
  color: #272727;
  font-weight: bold;
}
#wrapper-navbar:hover, #wrapper-navbar:focus {
  background: #fff;
}
#wrapper-navbar:hover .menu_container li a, #wrapper-navbar:focus .menu_container li a {
  color: #272727;
}
#wrapper-navbar a {
  color: #222 !important;
  font-size: 0.85rem !important;
  font-weight: 600 !important;
  text-decoration: none !important;
  padding: 0.3rem 0.8rem !important;
  display: flex !important;
  align-items: center !important;
  background: transparent !important;
}
#wrapper-navbar a:hover, #wrapper-navbar a:focus {
  color: #bc3365 !important;
  text-decoration: none !important;
}
#wrapper-navbar .header-lang-select {
  margin-right: 1rem;
}
#wrapper-navbar .header-lang-select select {
  border: none;
  background: #f8f8f8;
  color: #222;
  font-size: 0.95rem;
  font-weight: 600;
  padding: 0.3rem 0.8rem;
  border-radius: 1.2rem;
  outline: none;
  cursor: pointer;
  transition: background color 0.3s ease;
}
#wrapper-navbar .header-lang-select select:hover, #wrapper-navbar .header-lang-select select:focus {
  background: #bc3365;
  color: #fff;
}

.menu_container {
  display: flex;
  align-items: stretch;
  min-height: 4rem;
}
.menu_container li a {
  text-transform: uppercase;
  text-decoration: none;
  transition: color 0.3s ease;
}
.menu_container .nav-item {
  height: 100%;
  display: flex;
  align-items: stretch;
}
.menu_container .nav-item a {
  height: 100%;
  display: flex;
  align-items: center;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
}
.menu_container .nav-item a:hover, .menu_container .nav-item a:focus {
  color: #bc3365;
}

.fullscreen-slider {
  min-height: 70vh;
  width: 100%;
  overflow: hidden;
  position: relative;
  /* Fade + stagger pour le bloc texte (colonnes) de chaque slide */
  /* état fermé (pas de delay pour la disparition) */
  /* Montrer avec un léger délai (stagger) quand le slide devient actif */
  /* reduced-motion */
}
.fullscreen-slider .carousel-item img {
  object-fit: cover;
  width: 100%;
  height: 70vh;
}
.fullscreen-slider .carousel-item > .container-xxl {
  z-index: 3;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
}
.fullscreen-slider .carousel-item > .container-xxl [class^=col-] {
  pointer-events: auto;
  opacity: 0;
  transform: translateX(30px);
  transition: opacity transform 1.5s cubic-bezier(0.2, 0.8, 0.2, 1);
}
.fullscreen-slider .carousel-item > .container-xxl [class^=col-] h2, .fullscreen-slider .carousel-item > .container-xxl [class^=col-] .h2 {
  margin: 0.5rem 0 2rem 0;
  letter-spacing: 1px;
  font-size: 2.5rem;
}
.fullscreen-slider .carousel-item > .container-xxl [class^=col-] h5, .fullscreen-slider .carousel-item > .container-xxl [class^=col-] .h5 {
  font-size: 0.75rem;
  letter-spacing: 0.2em;
  line-height: 1.25;
  margin-bottom: 0;
}
.fullscreen-slider .carousel-item > .container-xxl [class^=col-] a {
  font-size: 1.2rem;
  letter-spacing: 1px;
}
.fullscreen-slider .carousel-item.active .container-xxl [class^=col-] {
  opacity: 1;
  transform: translateX(0);
}
.fullscreen-slider .carousel-item.active .container-xxl [class^=col-]:nth-child(1) {
  transition-delay: 120ms;
}
.fullscreen-slider .carousel-item.active .container-xxl [class^=col-]:nth-child(2) {
  transition-delay: 240ms;
}
.fullscreen-slider .carousel-item.active .container-xxl [class^=col-]:nth-child(3) {
  transition-delay: 360ms;
}
.fullscreen-slider .carousel-item.active .container-xxl [class^=col-]:nth-child(4) {
