.fullscreen-slider .carousel-item > .container-xxl [class^=col-] {
  opacity: 0;
  transform: translateY(50px);
  pointer-events: none;
  will-change: opacity, transform;
  transition: opacity 1500ms cubic-bezier(0.2, 0.8, 0.2, 1), transform 1500ms cubic-bezier(0.2, 0.8, 0.2, 1);
  transition-delay: 0s;
}
.fullscreen-slider .carousel-item.active > .container-xxl [class^=col-]:nth-child(1),
.fullscreen-slider .carousel-item.carousel-item-start > .container-xxl [class^=col-]:nth-child(1),
